export default {
	props: {
		item: Object,
		index: Number
	},
	methods: {
		open() {
			switch (this.item.clicktype) {
				case 'navigateTo':
					if (this.item.url) {
						uni.navigateTo({
							url: this.item.url
						});
					}
					break;
				case 'switchTab':
					if (this.item.url) {
						uni.switchTab({
							url: this.item.url
						})
					}
					break;
				case 'bind':
					if (this.user.userbind[this.item.provider]) return;
					this.bindother();
					break;
			}
		},
		// 绑定第三方登录
		bindother() {
			uni.login({
				provider: this.item.provider,



				success: (res) => {
					uni.getUserInfo({
						provider: this.item.provider,
						success: (infoRes) => {
							let options = Object.assign(infoRes, res);
							this.bindEvent(this.user.__formatOtherlogin(this.item.provider, options));
						}
					});
				},
				fail: (err) => {
					uni.showToast({
						title: '绑定失败',
						icon: "none"
					});
					console.log('login fail:', err);
				}
			});
		},
		async bindEvent(data) {
			uni.showLoading({
				title: '绑定中...',
				mask: false
			});
			let [err, res] = await this.$http.post("/user/bindother", data, {
				token: true,
				checktoken: true
			})
			if (!this.$http.errorcheck(err, res)) return uni.hideLoading();
			// 绑定成功
			uni.hideLoading();
			uni.showToast({
				title: '绑定成功！'
			});
			// 修改状态，缓存
			this.user.userbind[this.item.provider] = {
				nickname: data.nickName
			}
			uni.setStorageSync("userbind", this.user.userbind);
			this.$emit('updateuserbind');
		}
		// dsada(){
		// 	uni.navigateTo({
		// 		url: '../../pages/user-set-userinfo/user-set-userinfo',
		// 		success: res => {},
		// 		fail: () => {},
		// 		complete: () => {}
		// 	});
		// }
	}
}
